// import tumaczenia (json)
import { text } from './packageLang';  

//przyciski, ktre umoliwiaj uytkownikom przeczanie si midzy jzykiem angielskim i polskim
const refs = {
    enLangBTN: document.getElementById('e-lang-en'),
    plLangBTN: document.getElementById('e-lang-pl'),
  };
  
// aktualizacja zawartoci tekstowej rnych elementw na stronie na podstawie wybranego jzyka
// sprawdza, czy obiekt text posiada waciwo odpowiadajc wybranemu lang - jeli tak, pobiera tumaczenia dla tego jzyka
function changeLanguageOnClick(lang) {
  if (text.hasOwnProperty(lang)) {
    const translations = text[lang];

//iterujemy po kadej parze klucz-warto w obiekcie translations i znajdujemy wszystkie elementy na stronie, ktre pasuj do key
    for (const key in translations) {
      if (translations.hasOwnProperty(key)) {
        const value = translations[key];
        const elements = document.querySelectorAll(key);
        elements.forEach((element) => {
          element.textContent = value;
        });
      }
    }
  }
}

refs.enLangBtn.addEventListener('click', () => {
  changeLanguageOnClick('en');
});

refs.plLangBtn.addEventListener('click', () => {
  changeLanguageOnClick('pl');
});